.profile__name {
  max-width: 100%;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin: 0;
  align-self: end;
}

@media screen and (max-width:1023px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
  }
}

@media screen and (max-width:767px) {
  .profile__name {
    text-align: center;
    margin: 0 0 0 43px;
  }
}
